"use client";

import React, { useEffect, useState } from "react";
import Sidebar from "./Sidebar";
import Topbar from "./Topbar";
import { usePathname } from "next/navigation";

export default function AppShell({
  children,
}: {
  children: React.ReactNode;
}) {
  const [collapsed, setCollapsed] = useState(false);
  const [mobileOpen, setMobileOpen] = useState(false);
  const pathname = usePathname();

  useEffect(() => {
    const v = localStorage.getItem("sidebar_collapsed");
    if (v) setCollapsed(v === "1");
  }, []);

  useEffect(() => {
    localStorage.setItem(
      "sidebar_collapsed",
      collapsed ? "1" : "0"
    );
  }, [collapsed]);

  // Determine background based on route
  const getBackgroundClass = () => {
    if (pathname.startsWith('/reports')) return 'bg-gradient-to-br from-blue-50 via-white to-emerald-50';
    if (pathname === '/') return 'bg-gradient-to-br from-blue-50 via-white to-emerald-50';
    if (pathname === '/dashboard') return 'bg-gradient-to-br from-slate-50 via-white to-blue-50';
    return 'bg-gradient-to-b from-gray-50 via-white to-gray-50';
  };

  return (
    <div className={`min-h-screen text-gray-900 transition-all duration-300 ${getBackgroundClass()}`}>
      <div className="flex h-screen overflow-hidden">
        {/* Desktop Sidebar */}
        <div className="hidden lg:block">
          <Sidebar
            collapsed={collapsed}
            onToggleCollapsed={() => setCollapsed((x) => !x)}
          />
        </div>

        {/* Mobile Sidebar Overlay */}
        {mobileOpen && (
          <div className="lg:hidden fixed inset-0 z-50">
            <div
              className="absolute inset-0 bg-black/60 backdrop-blur-sm transition-opacity duration-300"
              onClick={() => setMobileOpen(false)}
            />
            <div className="absolute inset-y-0 left-0 w-80 max-w-full bg-white shadow-2xl transform transition-transform duration-300 ease-out">
              <Sidebar
                collapsed={false}
                onToggleCollapsed={() => {}}
                onNavigate={() => setMobileOpen(false)}
              />
            </div>
          </div>
        )}

        {/* Main Content */}
        <div className="flex-1 flex flex-col min-w-0 overflow-hidden">
          <Topbar
            collapsed={collapsed}
            onToggleSidebar={() => setCollapsed((x) => !x)}
            onOpenMobile={() => setMobileOpen(true)}
          />

          <main className="flex-1 overflow-y-auto overflow-x-hidden p-4 md:p-6 lg:p-8 scroll-smooth">
            <div className="max-w-full mx-auto animate-fadeIn">
              {children}
            </div>
          </main>

          {/* Floating Action Button for Mobile */}
          {!mobileOpen && (
            <button
              onClick={() => setMobileOpen(true)}
              className="lg:hidden fixed bottom-6 right-6 z-40 w-14 h-14 rounded-full bg-gradient-to-r from-blue-600 to-blue-700 shadow-2xl flex items-center justify-center transition-all duration-300 hover:scale-110 hover:shadow-3xl active:scale-95"
            >
              <svg className="w-6 h-6 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
              </svg>
            </button>
          )}
        </div>
      </div>
    </div>
  );
}